import { css } from '@emotion/react';

import Stack from './Stack';

const wrapper = css`
  display: flex;
  justify-content: space-between;
`;

interface VerticalScrollableBoxProps {
  children: React.ReactNode;
}

const HorizontalScrollableBox = ({
  children,
  ...others
}: VerticalScrollableBoxProps): JSX.Element => {
  return (
    <Stack direction="row" css={wrapper} {...others}>
      {children}
    </Stack>
  );
};

export default HorizontalScrollableBox;
